<script setup lang="ts">
import {baseDetailValuesType} from "@/store/capacity/types";
import {computed, ComputedRef} from "vue";
import store from "@/store";
import {formatNumber} from "@/utils/formatNumber";

const props = defineProps<{
  data: baseDetailValuesType
  classes: string[]
}>();

const timeMoney: ComputedRef<boolean> = computed(() => store.getters["capacityRepresentation/getTimeMoney"])

const ceilValue: ComputedRef<number> = computed(() => {
  if (timeMoney.value) {
    return Math.round((props.data.totalTimeMinutes / 60) * 10) / 10;
  } else {
    return props.data.totalSalary
  }
})

const sigma: ComputedRef<string> = computed(() => timeMoney.value ? ' h' : ' ₽')
</script>

<template>
  <td v-if="data.totalTimeMinutes !== 0"
      class="text-center p-2 border select-none text-black"
      :class="classes">
    <span v-if="timeMoney">{{ ceilValue }}{{ sigma }}</span>
    <span v-else>{{ formatNumber(ceilValue) }}{{ sigma }}</span>

  </td>
  <td v-else
      class="text-center text-sm p-2 border select-none"
      :class="classes">


  </td>
</template>
