<script setup lang="ts">

import CapacityCeil from "@/components/Capacity/CapacityCeil/CapacityCeil.vue";
import {byUserData} from "@/store/capacity/types";
import {ToggleCeilEventType} from "@/components/Capacity/types";
import {computed, ComputedRef, Ref, ref} from "vue";
import store from "@/store";
import CapacityDetailModule from "@/components/Capacity/CapacityDetailModule/CapacityDetailModule.vue";
import InactiveUserIcon from "@/components/Capacity/CapacityLine/InactiveUserIcon.vue";

// props
// ---------------------------------------------------------------------------------------------------------------------

const props = defineProps<{
  userData: byUserData
  showDetails: boolean,
}>();

// emits
// ---------------------------------------------------------------------------------------------------------------------
const emit = defineEmits<{
  (e: 'toggle-ceil', event: ToggleCeilEventType): void
  (e: 'toggle-detail', event: null): void
}>();

// getters
// ---------------------------------------------------------------------------------------------------------------------
const collCount: ComputedRef<number> = computed(() => store.getters["capacityData/getColumnCount"])

// vars
// ---------------------------------------------------------------------------------------------------------------------

const detailedCeils: Ref<Set<number>> = ref(new Set<number>())

const toggleHide = computed(() => {
  if (!props.showDetails) {
    detailedCeils.value = new Set();
  }
})

// procedures
// ---------------------------------------------------------------------------------------------------------------------
function toggleDetailedCeils(ceilNumber: number): void {
    emit('toggle-detail', null)
  if (detailedCeils.value.has(ceilNumber)) {
    detailedCeils.value.delete(ceilNumber)
  } else {
    detailedCeils.value.add(ceilNumber)
  }
}

function toggleAllCeils(): void {
  emit('toggle-detail', null)
  if (detailedCeils.value.size !== collCount.value) {
    detailedCeils.value = new Set(Array(collCount.value).fill(0).map((_, index) => index + 1));
  } else {
    detailedCeils.value = new Set();
  }
}

</script>

<template>
  <tr class="cursor-pointer text-black">
    <td class="bg-white min-w-4 sticky left-0">{{toggleHide}}</td>
    <td class="sticky left-3 bg-gray-200 text-base p-2 name_ceil select-none shadow-md w-40"
        @click="toggleAllCeils()">
      <div class="flex flex-nowrap">
        <div class="grow align-middle">{{ userData.userName }}</div>
        <InactiveUserIcon :user-active="userData.userActive"/>
      </div>
    </td>
    <CapacityCeil
        v-for="(ceilData, i) in userData.data"
        :ceil-data="ceilData"
        @toggle-ceil="emit('toggle-ceil', $event)"
        @toggle-detail="toggleDetailedCeils($event)"
        :ceil-number="i+1"
        :detailed-ceils="detailedCeils"/>
  </tr>
  <CapacityDetailModule v-if="(detailedCeils.size !== 0) && props.showDetails"
                        :selected-numbers="detailedCeils"
                        :user-id="userData.userId"/>
</template>