<script setup lang="ts">
import {computed, ComputedRef} from "vue";
import store from "@/store";
import {byUserData} from "@/store/capacity/types";
import {ToggleCeilEventType} from "@/components/Capacity/types";
import CapacityLine from "@/components/Capacity/CapacityLine/CapacityLine.vue";

// props
// ---------------------------------------------------------------------------------------------------------------------

const props = defineProps<{
  teamId: number,
  showDetails: boolean,
}>();

// emits
// ---------------------------------------------------------------------------------------------------------------------
const emit = defineEmits<{
  (e: 'toggle-ceil', event: ToggleCeilEventType): void
  (e: 'toggle-detail', event: null): void
}>();

const teamData: ComputedRef<byUserData[]> = computed(() => store.getters["capacityData/getByUserData"]
    .filter((x: byUserData) => x.teamId === props.teamId)
    .sort((a: byUserData, b: byUserData) => a.userName === b.userName))


</script>

<template>
  <table class="h-full">
    <CapacityLine v-for="userData in teamData" :key="userData.userId"
                  :user-data="userData"
                  @toggle-ceil="emit('toggle-ceil', $event)"
                  @toggle-detail="emit('toggle-detail', null)"
                  :show-details="props.showDetails"/>
  </table>
</template>