type Color = {
    r: number;
    g: number;
    b: number;
};

function interpolateColor(color1: Color, color2: Color, factor: number): Color {
    return {
        r: Math.round(color1.r + (color2.r - color1.r) * factor),
        g: Math.round(color1.g + (color2.g - color1.g) * factor),
        b: Math.round(color1.b + (color2.b - color1.b) * factor)
    };
}

function colorValueToHex(color: Color): string {
    function componentToHex(c: number): string {
        const hex = c.toString(16);
        return hex.length == 1 ? '0' + hex : hex;
    }

    return `#${componentToHex(color.r)}${componentToHex(color.g)}${componentToHex(color.b)}`;
}

export function getColorForValue(value: number, colorNegative: Color, colorZero: Color, colorPositive: Color): string {
    if (value <= -100) return colorValueToHex(colorNegative);
    if (value >= 100) return colorValueToHex(colorPositive);

    if (value < 0) {
        // Интерполяция между colorNegative и colorZero
        let factor = (value + 100) / 100; // Преобразование диапазона [-100, 0] в [0, 1]
        return colorValueToHex(interpolateColor(colorNegative, colorZero, factor));
    } else {
        // Интерполяция между colorZero и colorPositive
        let factor = value / 100; // Преобразование диапазона [0, 100] в [0, 1]
        return colorValueToHex(interpolateColor(colorZero, colorPositive, factor));
    }
}
