export function formCheck(payload: {
    firstName: string | null,
    lastName: string | null,
    fatherName: string | null,
    email: string | null,
    phone: string | null,
    login: string | null,
    password: string | null,
    salary: number,
    salaryType: number,
    asanaGid: number | null,
    teamId: number,
}): boolean {
    if (payload.firstName === null) {
        alert('first_name is empty')
        return false
    }

    if (payload.firstName.length < 2) {
        alert('first_name is short')
        return false;
    }

    if (payload.firstName.length > 256) {
        alert('first_name is big')
        return false;
    }

    if (payload.lastName === null) {
        alert('last_name is empty')
        return false
    }

    if (payload.lastName.length < 2) {
        alert('last_name is short')
        return false;
    }

    if (payload.lastName.length > 256) {
        alert('last_name is big')
        return false;
    }

    if (payload.fatherName !== null) {
        if (payload.fatherName.length > 256) {
            alert('father_name is big')
            return false;
        }
    }

    if (payload.phone !== null) {
        if (payload.phone.length > 256) {
            alert('phone is big')
            return false;
        }
    }

    if (payload.email !== null) {
        if (payload.email.length > 1024) {
            alert('email is big')
            return false;
        }
    }

    if (payload.login === null) {
        alert('login is empty')
        return false
    }

    if (payload.login.length < 2) {
        alert('login is short')
        return false;
    }

    if (payload.login.length > 128) {
        alert('login is big')
        return false;
    }

    if (payload.password === null) {
        alert('password is empty')
        return false
    }

    if (payload.password.length < 2) {
        alert('password is short')
        return false;
    }

    if (payload.password.length > 128) {
        alert('password is big')
        return false;
    }

    if (payload.salary === 0) {
        alert('salary is 0')
        return false;
    }

    if (payload.teamId == 0 || payload.teamId == null) {
        alert('need select team')
        return false;
    }

    return true;
}

