import {AsanaUserType, TeamType} from "@/components/Users/types";
import axios from "axios";
import {API_HOST} from "@/settings";
import {userDataModuleStateType, userDataModuleType} from "@/store/user/types";
import {userDataContext} from "@/store/types";


const userDataModule: userDataModuleType = {
        state: () => ({
            teamList: [],
            emptyAsanaUsersList: []
        }),


        mutations: {
            setTeamList(state: userDataModuleStateType, teamList: TeamType[]): void {
                state.teamList = teamList;
            },
            setEmptyAsanaUsersList(state: userDataModuleStateType, UserList: AsanaUserType[]): void {
                state.emptyAsanaUsersList = UserList
            }
        },

        getters: {
            getTeamList(state: userDataModuleStateType): TeamType[] {
                return state.teamList
            },
            getEmptyAsanaUsersList(state: userDataModuleStateType): AsanaUserType[] {
                return state.emptyAsanaUsersList
            }
        },

        actions: {
            async loadTeamList({state, commit}: userDataContext): Promise<void> {
                const getData = await axios.get(+ API_HOST + '/team/')
                commit('setTeamList', getData.data);
            },
            async loadEmptyAsanaUsersList({state, commit}: userDataContext): Promise<void> {
                const getData = await axios.get(API_HOST + '/user/empty/')
                commit('setEmptyAsanaUsersList', getData.data);
            },
        },

        namespaced: true
    }
;

export default userDataModule;