function getISOWeeksInYear(year: number): 53 | 52 {
    // Создаем дату, соответствующую 1 января указанного года
    const startOfYear = new Date(year, 0, 1);
    // Создаем дату, соответствующую 31 декабря того же года
    const endOfYear = new Date(year, 11, 31);

    // Получаем день недели для 1 января (0 - воскресенье, 1 - понедельник, ..., 6 - суббота)
    const startDay = startOfYear.getDay();
    // Получаем день недели для 31 декабря
    const endDay = endOfYear.getDay();

    // Проверяем критерии для 53 недель по ISO 8601
    // Год должен начинаться с понедельника или если високосный, то с воскресенья или понедельника
    // А должен заканчиваться с воскресенья или понедельника
    if ((startDay === 1) || (startDay === 0 && endDay === 1) || (startDay === 0 && endDay === 0)) {
        return 53;
    } else {
        return 52;
    }
}

export default getISOWeeksInYear;