<script setup lang="ts">
import Password from 'primevue/password';
import InputText from 'primevue/inputtext';
import FloatLabel from 'primevue/floatlabel';
import Button from 'primevue/button';
import {Ref, ref} from "vue";
import axios from "axios";
import {API_HOST} from "@/settings";
import router from "@/router";

// consts
// ---------------------------------------------------------------------------------------------------------------------

const login: Ref<string> = ref('')
const password: Ref<string> = ref('')

// procedures
// ---------------------------------------------------------------------------------------------------------------------

async function authorize() {

  if (!login.value || !password.value) return
  if (login.value.length == 0 || login.value.length > 128
      || password.value.length == 0 || password.value.length > 128) return

  try {
    await axios.post(API_HOST + '/auth/', {
      login: login.value,
      password: password.value,
    })
    console.log('authorize success')

  } catch (error) {
    console.log('authorize failed')
    return
  }
  console.log('load user data')
  // await store.dispatch('meData/loadMeData')
  console.log('redirect to home')
  await router.push('/');

}


</script>

<template>
  <div class="bg-gray-100 min-h-[100vh] flex justify-center items-center">
    <div class="max-w-fit flex-col shadow-lg bg-white rounded-lg px-5 pb-5">
      <div class="mt-10">
        <FloatLabel>
          <InputText id="username" v-model="login"/>
          <label for="username">Логин</label>
        </FloatLabel>
      </div>
      <div class="mt-10">
        <FloatLabel>
          <Password id="password" v-model="password" :feedback="false"/>
          <label for="password">Пароль</label>
        </FloatLabel>
      </div>
      <div class="flex justify-center mt-10">
        <Button label="Войти" @click="authorize()"/>
      </div>
    </div>
  </div>
</template>