import {meDataModuleStateType, meDataModuleType, meDataType} from "@/store/me/types";
import {meDataContext} from "@/store/types";
import axios from "axios";
import {API_HOST} from "@/settings";
import {byUserData} from "@/store/capacity/types";


const meDataModule: meDataModuleType = {
    state: () => ({
        me: {
            is_active: false,
            is_daemon: false,
            email: null,
            phone: null,
            login: '',
            first_name: '',
            last_name: '',
            father_name: null,
            asana_gid: null,
            asana_name: null,
            asana_empty_user: false,
            team_id: 0,
            team_lead: false,
            observer: false,
            show_money: false,
        }
    }),


    mutations: {
        setMeData(state: meDataModuleStateType, data: meDataType): void {
            state.me = data
        }
    },

    getters: {
        getMe(state: meDataModuleStateType): meDataType {
            return state.me
        }
    },

    actions: {
        async loadMeData({state, commit}: meDataContext): Promise<void> {
            state.me = await axios.get(API_HOST + '/me/')
        }
    },

    namespaced: true
};

export default meDataModule;