<script setup lang="ts">
import {teamData} from "@/store/capacity/types";
import {ref, Ref} from "vue";
import {ToggleCeilEventType} from "@/components/Capacity/types";
import CapacityTeamData from "@/components/Capacity/CapacityTeamData/CapacityTeamData.vue";

const emit = defineEmits<{
  (e: 'toggle-ceil', event: ToggleCeilEventType): void
}>();

const props = defineProps<{
  teamInfo: teamData
}>()
const teamDetailShow: Ref<boolean> = ref(false)

function hideDetailShow() {
  teamDetailShow.value = false;
}

function showDetailShow() {
  teamDetailShow.value = true
}
</script>

<template>
  <div>

    <div class="h-fit min-w-fit max-w-fit text-nowrap mx-4 my-2 shadow-lg bg-white rounded-lg py-4 pr-4 pl-1">
      <div class="sticky left-3 pb-2 font-bold text-xl text-left w-40 bg-white text-black ml-4">
          <span class="text-2xl select-none"
                :class="teamDetailShow ? ['cursor-pointer'] : []"
                @click="hideDetailShow()">
            {{ props.teamInfo.teamName }}
          </span>
      </div>
      <div>
        <CapacityTeamData
            :team-id="props.teamInfo.teamId"
            @toggle-ceil="emit('toggle-ceil', $event)"
            @toggle-detail="showDetailShow()"
            :show-details="teamDetailShow"/>
      </div>
    </div>
  </div>
</template>
