<script setup lang="ts">
import Select from 'primevue/select';
import {Ref, ref, ComputedRef, computed, watch} from "vue";
import {AsanaUserType} from "@/components/Users/types";
import store from "@/store";

const props = defineProps<{
  disabled: boolean
  issetAsanaUser: AsanaUserType | null
}>();

const emit = defineEmits<{
  (e: 'change', event: AsanaUserType | null): void
}>();

// @ts-ignore
const UsersList: ComputedRef<AsanaUserType[]> = computed(() => {
  if (props.issetAsanaUser) {
    return [...store.getters["userData/getEmptyAsanaUsersList"], props.issetAsanaUser]
  } else {
    return store.getters["userData/getEmptyAsanaUsersList"]
  }
})

const selectedUserGid: Ref<number | null> = ref(props.issetAsanaUser ? props.issetAsanaUser.gid : null)

watch(() => props.issetAsanaUser, (newAsanaUser) => {
  selectedUserGid.value = newAsanaUser ? newAsanaUser.gid : null;
});

</script>

<template>

  <Select v-model="selectedUserGid"
          :options="UsersList"
          optionLabel="name"
          optionValue="gid"
          placeholder=""
          class="w-full w-[300px]"
          filter
          showClear
          @change="emit('change',
          selectedUserGid=== null ? null : UsersList.filter(user => user.gid === selectedUserGid)[0])"
          :disabled="props.disabled"/>

</template>