<script setup lang="ts">
import Select from 'primevue/select';
import {Ref, ref, ComputedRef, computed, watch} from "vue";
import {TeamType} from "@/components/Users/types";
import store from "@/store";

const props = defineProps<{
  issetTeamId: number,
  disabled: boolean
}>();

const emit = defineEmits<{
  (e: 'change', event: TeamType): void
}>();

// @ts-ignore
const teamList: ComputedRef<TeamType[]> = computed(() => store.getters["userData/getTeamList"])
// @ts-ignore
const selectedTeamId: Ref<number> = ref(props.issetTeamId)

watch(() => props.issetTeamId, (newTeamId) => {
  selectedTeamId.value = newTeamId;
});

</script>

<template>

  <Select v-model="selectedTeamId"
          :options="teamList"
          optionLabel="name"
          optionValue="id"
          placeholder=""
          class="w-full md:w-56"
          @change="emit('change', teamList.filter(team => team.id === selectedTeamId)[0])"
          :disabled="disabled"/>

</template>