<script setup lang="ts">
import Select from 'primevue/select';
import {Ref, ref, ComputedRef, computed} from "vue";
import {AsanaUserType} from "@/components/Users/types";
import store from "@/store";

const emit = defineEmits<{
  (e: 'change', event: AsanaUserType | null): void
}>();

// @ts-ignore
const UsersList: ComputedRef<AsanaUserType[]> = computed(() => store.getters["userData/getEmptyAsanaUsersList"])

const selectedUserGid: Ref<number | null> = ref(null)

</script>

<template>

  <Select v-model="selectedUserGid"
          :options="UsersList"
          optionLabel="name"
          optionValue="gid"
          placeholder="Выберите пользователя Asana"
          class="w-full w-[300px]"
          filter
          showClear
          @change="emit('change',
          selectedUserGid=== null ? null : UsersList.filter(user => user.gid === selectedUserGid)[0])"/>

</template>