<script setup lang="ts">

import InputText from "primevue/inputtext";
import RadioButton from "primevue/radiobutton";
import TeamSelector from "@/components/Users/CreateUserForm/TeamSelector/TeamSelector.vue";
import AsanaUserSelector from "@/components/Users/CreateUserForm/AsanaUserSelector/AsanaUserSelector.vue";
import Button from "primevue/button";
import InputNumber from "primevue/inputnumber";
import {AsanaUserType, TeamType} from "@/components/Users/types";
import axios from "axios";
import {API_HOST, axiosStandartConfig} from "@/settings";
import {ref, Ref} from "vue";
import {formCheck} from "@/components/Users/CreateUserForm/formCheckFunc";


const firstName: Ref<string | null> = ref('')
const lastName: Ref<string | null> = ref('')
const fatherName: Ref<string | null> = ref('')
const email: Ref<string | null> = ref('')
const phone: Ref<string | null> = ref('')
const login: Ref<string | null> = ref('')
const password: Ref<string | null> = ref('')
const salary: Ref<number> = ref(0)
const salaryType: Ref<number> = ref(1)
const asanaGid: Ref<number | null> = ref(null)
const teamId: Ref<number> = ref(0)

function checkAsanaEmail(data: AsanaUserType | null) {
  if (data !== null) {

    email.value = data.email
    asanaGid.value = data.gid
  } else {
    asanaGid.value = null
    email.value = null
  }
}

function changeTeamId(data: TeamType) {
  teamId.value = data.id
}

function cleanFields() {
  firstName.value = ''
  lastName.value = ''
  fatherName.value = ''
  email.value = ''
  phone.value = ''
  login.value = ''
  password.value = ''
  salary.value = 0
  salaryType.value = 1
  asanaGid.value = null
  teamId.value = 0
}

async function createUser() {
  if (!formCheck({
    firstName: firstName.value,
    lastName: lastName.value,
    fatherName: fatherName.value,
    email: email.value,
    phone: phone.value,
    login: login.value,
    password: password.value,
    salary: salary.value,
    salaryType: salaryType.value,
    asanaGid: asanaGid.value,
    teamId: teamId.value,
  })) {
    return
  }

  const result = await axios.post(API_HOST + '/user/', {
        email: email.value,
        phone: phone.value,
        login: login.value,
        password: password.value,
        first_name: firstName.value,
        last_name: lastName.value,
        father_name: fatherName.value,
        asana_gid: asanaGid.value,
        team_id: teamId.value,
        salary: salary.value,
        salary_type: salaryType.value
      },
      axiosStandartConfig)

  if (result.status === 200) {
    cleanFields()
  } else {
    alert(result.statusText)
  }
}

</script>

<template>
  <div class="w-fit">
    <div class="flex nowrap mt-5">
      <TeamSelector class="mx-5" @change="changeTeamId($event)"/>
      <div class="w-[300px] ">
        <AsanaUserSelector @change="checkAsanaEmail($event)"/>
      </div>
    </div>
    <div class="flex nowrap  mt-5">
      <div class="mx-5">
        Имя <br>
        <InputText id="first_name" v-model="firstName"/>
      </div>
      <div class="mx-5">
        Фамилия <br>
        <InputText id="last_name" v-model="lastName"/>
      </div>
      <div class="mx-5">
        Отчество <br>
        <InputText id="father_name" v-model="fatherName"/>
      </div>
    </div>

    <div class="flex nowrap mt-5">
      <div class="mx-5">
        Почта <br>
        <InputText id="email" v-model="email"/>
      </div>
      <div class="mx-5">
        Телефон <br>
        <InputText id="phone" v-model="phone"/>
      </div>
    </div>

    <div class="flex nowrap mt-5">
      <div class="mx-5">
        Логин<br>
        <InputText id="login" v-model="login"/>
      </div>
      <div class="mx-5">
        Пароль<br>
        <InputText id="password" v-model="password"/>
      </div>
    </div>

    <div class="flex nowrap mt-5">
      <div class="mx-5">
        Оклад<br>
        <InputNumber v-model="salary" fluid/>
      </div>
      <div class="flex flex-wrap gap-4 mx-5">
        <div class="flex items-center">
          <RadioButton v-model="salaryType" name="Месяц" :value="1" inputId="salary_month"/>
          <label for="salary_month" class="ml-2">Месяц</label>
        </div>
        <div class="flex items-center">
          <RadioButton v-model="salaryType" name="Час" :value="2" inputId="salary_hour"/>
          <label for="salary_hour" class="ml-2">Час</label>
        </div>
      </div>
    </div>

    <div class="flex mt-5 justify-center">
      <Button label="Создать" @click="createUser()"/>
    </div>
  </div>
</template>

