import {BaseDetailDataType, baseDetailValuesType, ScaleType, variousDetailsDataType} from "@/store/capacity/types";
import {formattedData, taskRecord} from "@/components/Capacity/CapacityDetailLine/CapacityDetailLineTypes";
import {
    detailFields,
    emptyData,
    initProjectAcc
} from "@/components/Capacity/CapacityDetailLine/CapacityDetailLineConsts";
import axios from "axios";
import {API_HOST} from "@/settings";

function getTaskRecords(
    taskIds: Set<number>,
    lineData: variousDetailsDataType[],
    collCount: number,
    scale: ScaleType
): taskRecord[] {
    let taskRecords: taskRecord[] = [];
    taskIds.forEach(taskId => {
        let taskRecord = [];

        for (let i = 1; i <= collCount; i++) {
            // @ts-ignore
            const dayRecords = lineData.filter(x => x[scale] === i && x.taskId === taskId);
            taskRecord.push(dayRecords.length > 0 ? dayRecords[0] : emptyData);
        }
        taskRecords.push({
            taskId: taskId,
            // @ts-ignore
            taskName: lineData.find(x => x.taskId === taskId)?.taskName,
            records: taskRecord
        });
    });
    return taskRecords;
}

function getUniqueProjectNames(items: BaseDetailDataType[]): Set<string> {
    return new Set(items.map(x => x.projectName));
}

function calculateProjectTotals(taskRecords: taskRecord[], collCount: number): baseDetailValuesType[] {
    let projectTotals: baseDetailValuesType[] = [];

    for (let i = 1; i <= collCount; i++) {

        let sum = taskRecords.reduce((acc, record) => {
            const dayRecord: BaseDetailDataType = record.records[i - 1];

            detailFields.forEach(key => {
                // @ts-ignore
                acc[key] += dayRecord[key];
            });
            return acc;
        }, {...initProjectAcc});
        projectTotals.push(sum);
    }
    return projectTotals;
}

export function makeDataFormat(
    lineData: variousDetailsDataType[],
    scale: ScaleType,
    collCount: number
): formattedData[] {
    let result: formattedData[] = [];

    const projects: Set<string> = getUniqueProjectNames(lineData);

    projects.forEach(projectName => {

        const tasksIds: Set<number> = new Set(lineData.filter(x => x.projectName === projectName).map((x: BaseDetailDataType) => x.taskId));
        let taskRecords = getTaskRecords(tasksIds, lineData, collCount, scale);

        result.push({
            projectName: projectName,
            tasks: taskRecords,
            records: calculateProjectTotals(taskRecords, collCount)
        });
    });
    return result;
}

export async function loadData(scale: ScaleType,
                               userId: number,
                               selectedNumbers: Set<number>,
                               year: number,
                               month: number
): Promise<variousDetailsDataType[]> {
    if (selectedNumbers.size === 0) {
        return []
    }

    const numbersString = [...selectedNumbers].join(',')

    if (scale === 'day') {

        return (await axios.get(API_HOST + '/detail_data/day/'
            + userId + '/' + year + '/' + month
            + '/?days=' + numbersString)).data

    } else if (scale === 'week') {
        return (await axios.get(API_HOST + '/detail_data/week/'
            + userId + '/' + year
            + '/?weeks=' + numbersString)).data

    } else if (scale === 'month') {
        // @ts-ignore
        return (await axios.get(API_HOST + '/detail_data/month/'
            + userId + '/' + year
            + '/?months=' + numbersString)).data


    } else {
        console.error('wrong scale value');
        // @ts-ignore
        return [];
    }
}