type UserNames = {
    first_name: string;
    last_name: string;
    father_name: string | null;
};

export function formatName({first_name, last_name, father_name}: UserNames): string {
    return `${last_name} ${first_name.charAt(0).toUpperCase() + '.'}${ father_name ? father_name.charAt(0).toUpperCase() + '.' : ''}`;

}
