<script setup lang="ts">
import Button from 'primevue/button';
import store from "@/store";
import {computed, ComputedRef} from "vue";
import {ScaleType} from "@/store/capacity/types";
import {MonthNames} from "@/utils/consts";

function SetPrevPeriod() {
  store.dispatch('capacityData/prevPeriod')
}

function setNextPeriod() {
  store.dispatch('capacityData/nextPeriod')
}

function setNowPeriod() {
  store.dispatch('capacityData/nowPeriod')
}

const year: ComputedRef<number> = computed(() => store.getters["capacityData/getYear"])
const month: ComputedRef<number> = computed(() => store.getters["capacityData/getMonth"])
const scale: ComputedRef<ScaleType> = computed(() => store.getters["capacityData/getScale"])

const dateString: ComputedRef<string> = computed(() => {
  if (scale.value === 'day') {
    return MonthNames[month.value - 1] + ' ' + year.value + 'г.'
  } else if (scale.value === 'week' || scale.value === 'month') {
    return year.value + ' г.'
  } else {
    console.error('wrong scale parameter')
    return ''
  }

})

</script>

<template>
  <div class="flex w-fill mr-[31px]">
    <Button
        icon="pi pi-arrow-left"
        aria-label="Предыдущий период"
        severity="secondary"
        class="h-[40px] self-center"
        @click="SetPrevPeriod()"/>

    <Button
        severity="secondary"
        :label="dateString"
        class="h-[40px] self-center "
        @click="setNowPeriod()"/>

    <Button
        icon="pi pi-arrow-right"
        aria-label="Следующий период"
        severity="secondary"
        class="h-[40px] self-center "
        @click="setNextPeriod()"/>
  </div>
</template>

<style scoped lang="scss">
.p-button:focus {
    outline: none;
}
</style>