import {capacityRepresentationModuleStateType} from "@/store/capacity/types";

const capacityRepresentationModule = {
    state: () => ({
        timeMoneySwitcher: true,
        percentsDigitsSwitcher: true
    }),


    mutations: {
        switchTimeMoney(state: capacityRepresentationModuleStateType): void {
            state.timeMoneySwitcher = !state.timeMoneySwitcher;
        },
        switchPercentsDigits(state: capacityRepresentationModuleStateType): void {
            state.percentsDigitsSwitcher = !state.percentsDigitsSwitcher;
        },

    },

    getters: {
        getTimeMoney(state: capacityRepresentationModuleStateType): boolean {
            return state.timeMoneySwitcher
        },
        getPercentsDigits(state: capacityRepresentationModuleStateType): boolean {
            return state.percentsDigitsSwitcher
        },
    },

    actions: {},

    namespaced: true
};

export default capacityRepresentationModule;