export function getWeekdayShortName(year: number, month: number, day: number): string {
    // Создаем объект Date с указанным годом, месяцем и днем
    // Обратите внимание, что месяц должен быть month - 1, так как в JS месяцы идут от 0 до 11
    const date: Date = new Date(year, month - 1, day);

    // Форматируем дату в русский двухбуквенный формат дня недели
    const weekday: string = date.toLocaleString('ru', {weekday: 'short'});

    // Возвращаем строку с первыми двумя буквами, переведенными в верхний регистр
    return weekday.charAt(0).toUpperCase() + weekday.charAt(1).toLowerCase();
}