function getDaysInMonth(year: number, month: number): number {
    // Корректируем месяц для соответствия формату JavaScript (0-11)
    const adjustedMonth = month - 1;
    // Проверяем, является ли месяц декабрем
    if (adjustedMonth === 11) { // Декабрь
        // Создаем объект Date для "дня 0" января следующего года
        const date = new Date(year + 1, 0, 0);
        return date.getDate();
    } else {
        // Создаем объект Date для "дня 0" следующего месяца
        const date = new Date(year, adjustedMonth + 1, 0);
        return date.getDate();
    }
}

export default getDaysInMonth;