<script setup lang="ts">
import {computed, ComputedRef, Ref, ref, watchEffect} from "vue";
import {ScaleType} from "@/store/capacity/types";
import store from "@/store";
import {formattedData} from "@/components/Capacity/CapacityDetailLine/CapacityDetailLineTypes";
import {loadData, makeDataFormat} from "@/components/Capacity/CapacityDetailLine/CapacityDetailLineFunc";
import CapacityDetailLine from "@/components/Capacity/CapacityDetailLine/CapacityDetailLine.vue";

const props = defineProps<{
  selectedNumbers: Set<number>,
  userId: number
}>();

const scale: ComputedRef<ScaleType> = computed(() => store.getters["capacityData/getScale"])
const year: ComputedRef<number> = computed(() => store.getters["capacityData/getYear"])
const month: ComputedRef<number> = computed(() => store.getters["capacityData/getMonth"])
const collCount: ComputedRef<number> = computed(() => store.getters["capacityData/getColumnCount"])


const selectedNumbers: ComputedRef<Set<number>> = computed(() => {
  return new Set(props.selectedNumbers.values());
});
const userId: ComputedRef<number> = computed(() => props.userId);

const data: Ref<formattedData[]> = ref([])
const loading = ref(false);
watchEffect(async () => {
  loading.value = true;
  try {
    const rawData = await loadData(
        scale.value,
        userId.value,
        selectedNumbers.value,
        year.value,
        month.value
    );
    data.value = makeDataFormat(rawData, scale.value, collCount.value);
  } catch (error) {
    console.error('Ошибка при загрузке данных:', error);
    // Обработка ошибок, например, можно задать data.value в пустой массив или значение по умолчанию
    data.value = []; // В зависимости от требований к логике обработки ошибок
  } finally {
    loading.value = false;
  }
});


</script>

<template>
  <CapacityDetailLine v-for="projectData in data" :data="projectData"/>
</template>

