<script setup lang="ts">
import Listbox from 'primevue/listbox';
import {computed, ComputedRef, onBeforeMount, Ref, ref} from "vue";
import axios from "axios";
import {API_HOST, axiosStandartConfig} from "@/settings";
import {formatName} from "@/utils/formatName";
import InputText from "primevue/inputtext";
import AsanaUserSelector from "@/components/Users/EditUserForm/AsanaUserSelector/AsanaUserSelector.vue";
import Button from "primevue/button";
import ToggleSwitch from 'primevue/toggleswitch';
import {AsanaUserType, TeamType, userShortType, userType} from "@/components/Users/types";
import TeamSelector from "@/components/Users/EditUserForm/TeamSelector/TeamSelector.vue";
import WorkChangesData from "@/components/Users/EditUserForm/WorkChangesData/WorkChangesData.vue";

// hooks
// ---------------------------------------------------------------------------------------------------------------------

onBeforeMount(async () => {
  const userData: {
    id: number,
    first_name: string,
    last_name: string,
    father_name: string | null
  }[] = (await axios.get(API_HOST + '/user?fields=id,first_name,last_name,father_name')).data
  usersList.value = userData.map(item => ({
    id: item.id,
    userName: formatName({
      first_name: item.first_name,
      last_name: item.last_name,
      father_name: item.father_name
    })
  })).sort((a, b) => a.userName.localeCompare(b.userName));
})

// consts
// ---------------------------------------------------------------------------------------------------------------------

const usersList: Ref<userShortType[]> = ref([])
const selectedUserShort: Ref<userShortType | null> = ref(null)
const selectedUser: Ref<userType | null> = ref(null)
const firstName: Ref<string> = ref('')
const lastName: Ref<string | null> = ref('')
const fatherName: Ref<string | null> = ref('')
const email: Ref<string | null> = ref('')
const phone: Ref<string | null> = ref('')
const login: Ref<string | null> = ref('')
const password: Ref<string | null> = ref(null)
const teamId: Ref<number> = ref(0)
const userActive: Ref<boolean> = ref(true)
const asanaUser: Ref<AsanaUserType | null> = ref(null)
const fieldBlock: ComputedRef<boolean> = computed(() => selectedUser.value === null)


// procedures
// ---------------------------------------------------------------------------------------------------------------------

async function userSelectChange() {
  if (selectedUserShort.value) {
    selectedUser.value = (await axios.get(API_HOST + '/user/' + selectedUserShort.value?.id + '/')).data

    if (selectedUser.value) {
      firstName.value = selectedUser.value.first_name
      lastName.value = selectedUser.value.last_name
      fatherName.value = selectedUser.value.father_name
      email.value = selectedUser.value.email
      phone.value = selectedUser.value.phone
      login.value = selectedUser.value.login
      teamId.value = selectedUser.value.team_id
      password.value = null
      userActive.value = selectedUser.value.is_active
      // @ts-ignore
      asanaUser.value = selectedUser.value.asana_gid ? {
        gid: selectedUser.value.asana_gid,
        name: selectedUser.value.asana_name,
        email: selectedUser.value.email
      } : null
    }
  } else {
    cleanFields()
  }

}

function cleanFields() {
  selectedUser.value = null
  firstName.value = ''
  lastName.value = ''
  fatherName.value = ''
  email.value = ''
  phone.value = ''
  login.value = ''
  teamId.value = 0
  password.value = null
  asanaUser.value = null
}

async function changeUser() {
  if (selectedUser.value) {
    const result = await axios.put(API_HOST + '/user/', {
          id: selectedUser.value.id,
          team_id: teamId.value,
          asana_gid: asanaUser.value ? asanaUser.value.gid : null,
          first_name: firstName.value,
          last_name: lastName.value,
          father_name: fatherName.value,
          email: email.value,
          phone: phone.value,
          login: login.value,
          password: password.value,
          is_active: userActive.value,
        },
        axiosStandartConfig)
    cleanFields();
  }

}

function changeAsanaUser(user: AsanaUserType | null) {
  asanaUser.value = user
}

function changeTeam(team: TeamType) {
  teamId.value = team.id
}

</script>

<template>

  <Listbox v-model="selectedUserShort"
           :options="usersList"
           filter
           optionLabel="userName"
           class="w-[200px]"
           scrollHeight="50rem"
           @change="userSelectChange()"/>
  <div class="flex bg-amber-500 flex-col">
    <div class="w-fit bg-fuchsia-400 h-fit mb-2">
      <div class="flex nowrap mt-5">

        <TeamSelector
            class="mx-5"
            :issetTeamId="teamId"
            :disabled="fieldBlock"
            @change="changeTeam($event)"/>

        <div class="w-[300px] ">

          <AsanaUserSelector
              @change="changeAsanaUser($event)"
              :disabled="fieldBlock"
              :issetAsanaUser="asanaUser"/>

        </div>
      </div>
      <div class="flex nowrap  mt-5">
        <div class="mx-5">
          Имя <br>
          <InputText id="first_name" v-model="firstName" :disabled="fieldBlock"/>
        </div>
        <div class="mx-5">
          Фамилия <br>
          <InputText id="last_name" v-model="lastName" :disabled="fieldBlock"/>
        </div>
        <div class="mx-5">
          Отчество <br>
          <InputText id="father_name" v-model="fatherName" :disabled="fieldBlock"/>
        </div>
      </div>

      <div class="flex nowrap mt-5">
        <div class="mx-5">
          Почта <br>
          <InputText id="email" v-model="email" :disabled="fieldBlock"/>
        </div>
        <div class="mx-5">
          Телефон <br>
          <InputText id="phone" v-model="phone" :disabled="fieldBlock"/>
        </div>
      </div>

      <div class="flex nowrap mt-5">
        <div class="mx-5">
          Логин<br>
          <InputText id="login" v-model="login" :disabled="fieldBlock"/>
        </div>
        <div class="mx-5">
          Пароль<br>
          <InputText id="login" v-model="password" :disabled="fieldBlock"/>
        </div>
        <div class="flex">
          <div class="flex self-center align-middle">
            Активен
            <ToggleSwitch v-model="userActive" :disabled="fieldBlock" class="ml-2"/>
          </div>
        </div>
      </div>


      <div class="flex mt-5 justify-center">
        <Button label="Изменить" @click="changeUser()"/>
      </div>
    </div>

    <div class="flex nowrap">
      <div>
        <WorkChangesData :user_id="selectedUser?.id"/>
      </div>

      <div>

      </div>
    </div>
  </div>
</template>

