<script setup lang="ts">

// props
// ---------------------------------------------------------------------------------------------------------------------
import {workDateType} from "@/components/Users/types";
import {ref, watch} from "vue";

const props = defineProps<{
  workId: number,
  date: string
}>();

// emits
// ---------------------------------------------------------------------------------------------------------------------
const emit = defineEmits<{
  (e: 'change', event: workDateType): void
}>();

// hooks
// ---------------------------------------------------------------------------------------------------------------------
watch(() => props.date, async (newDate) => {
  currentDate.value = newDate
});

// consts
// ---------------------------------------------------------------------------------------------------------------------

const currentDate = ref(props.date);
</script>

<template>

  <input type="date"
         v-model="currentDate"
         @input="emit('change', {id: props.workId, date: $event.target.value})"/>

</template>

