<script setup lang="ts">
import {BaseDataType} from "@/store/capacity/types";
import {computed, ComputedRef} from "vue";
import store from "@/store";
import {getColorForValue} from "@/utils/getColorForValue";
import convertMinutesToHours from "@/utils/convertMinutesToHours";
import {ToggleCeilEventType} from "@/components/Capacity/types";
import {formatNumber} from "@/utils/formatNumber";

// props
// ---------------------------------------------------------------------------------------------------------------------

const props = defineProps<{
  ceilData: BaseDataType,
  ceilNumber: number,
  detailedCeils: Set<number>,
}>();

// emits
// ---------------------------------------------------------------------------------------------------------------------
const emit = defineEmits<{
  (e: 'toggle-ceil', event: ToggleCeilEventType): void
  (e: 'toggle-detail', event: number): void
}>();

// getters
// ---------------------------------------------------------------------------------------------------------------------

const percentsDigits: ComputedRef<boolean> = computed(() => store.getters["capacityRepresentation/getPercentsDigits"])
const timeMoney: ComputedRef<boolean> = computed(() => store.getters["capacityRepresentation/getTimeMoney"])

// vars
// ---------------------------------------------------------------------------------------------------------------------

const sigma: ComputedRef<string> = computed(() => {
  if (percentsDigits.value) {
    return '%'
  }
  if (timeMoney.value) {
    return ' h'
  }
  return ' ₽'
})

const ceilValue: ComputedRef<number> = computed(() => {
      if (props.ceilData) {
        if (percentsDigits.value) {
          if (timeMoney.value) {
            return props.ceilData.deltaPercent
          } else {
            return props.ceilData.deltaPercentMoney
          }
        } else {
          if (timeMoney.value) {
            return convertMinutesToHours(props.ceilData.timePlanMin - props.ceilData.timeFactMin)
          } else {
            return props.ceilData.moneyPlan - props.ceilData.moneyFact
          }
        }

      } else {
        return 0;
      }
    }
)

const ceilColor = computed(() => {
      let value

      if (timeMoney.value) {
        value = props.ceilData.deltaPercent
      } else {
        value = props.ceilData.deltaPercentMoney
      }

      return getColorForValue(
          value,
          {r: 138, g: 181, b: 230},
          {r: 255, g: 255, b: 255},
          {r: 230, g: 115, b: 131},
      )
    }
)

const isDetailed = computed(() => props.detailedCeils.has(props.ceilNumber))

</script>

<template>
  <td
      v-if="props.ceilData"
      class="my-ceil text-center bg-white text-base p-2 border select-none"
      :class="isDetailed ? ['shadow-[inset_0_5px_10px_-2px_rgba(0,0,0,0.2)]'] : []"
      :style="{ backgroundColor: ceilColor }"
      @mouseenter="emit('toggle-ceil', {event: $event, data: props.ceilData, show: true, type: 'mainData'})"
      @mouseleave="emit('toggle-ceil', {event: $event, data: undefined, show: false, type: 'mainData'})"
      @click="emit('toggle-detail', ceilNumber)">

    <span class="text-base" v-if="(!percentsDigits) && (!timeMoney)">{{ formatNumber(ceilValue) }}{{ sigma }}</span>
    <span class="text-base" v-else>{{ ceilValue }}{{ sigma }}</span>

  </td>
  <td v-else class="my-ceil border"></td>

</template>

<style scoped lang="scss">
.my-ceil {
  min-width: var(--ceil-widh);
  max-width: var(--ceil-widh);
}
</style>