<script setup lang="ts">
import {ref, watch} from "vue";
import InputText from 'primevue/inputtext';
import {workCommentType} from "@/components/Users/types";


// props
// ---------------------------------------------------------------------------------------------------------------------
const props = defineProps<{
  workId: number,
  workComment: string
}>();

// emits
// ---------------------------------------------------------------------------------------------------------------------
const emit = defineEmits<{
  (e: 'change', event: workCommentType): void
}>();


// hooks
// ---------------------------------------------------------------------------------------------------------------------
watch(() => props.workComment, (newComment) => {
  commentData.value = newComment;
});

// consts
// ---------------------------------------------------------------------------------------------------------------------
const commentData = ref(props.workComment)


</script>

<template>
  <InputText type="text" v-model="commentData" @change="emit('change', {id:props.workId, text:commentData})"/>
</template>

