<script setup lang="ts">

import DataTable from "primevue/datatable";
import Column from "primevue/column";
import {onBeforeMount, Ref, ref, watch} from "vue";
import {workChangeType, workCommentType, workDateType, workTypeType} from "@/components/Users/types";
import axios from "axios";
import {API_HOST} from "@/settings";
import CommentField from "@/components/Users/EditUserForm/WorkChangesData/CommentField/CommentField.vue";
import DateField from "@/components/Users/EditUserForm/WorkChangesData/DateField/DateField.vue";

// props
// ---------------------------------------------------------------------------------------------------------------------

const props = defineProps<{
  user_id: number | undefined,
}>();

// hooks
// ---------------------------------------------------------------------------------------------------------------------

onBeforeMount(async () => {
  if (props.user_id) {
    workData.value = (await axios.get(API_HOST + '/work_changes/' + props.user_id + '/')).data
    loaded.value = true
  }
})

watch(() => props.user_id, async (newUserId) => {
  if (props.user_id) {
    workData.value = []
    workData.value = (await axios.get(API_HOST + '/work_changes/' + props.user_id + '/')).data
    loaded.value = true
  } else {
    workData.value = []
    loaded.value = false
  }

});
// consts
// ---------------------------------------------------------------------------------------------------------------------

const loaded: Ref<boolean> = ref(false)
const workData: Ref<workChangeType[]> = ref([])
const axiosConfig = {
  headers: {'Content-Type': 'application/json'},
  withCredentials: true
}

// procedures
// ---------------------------------------------------------------------------------------------------------------------
async function commentChange({id, text}: workCommentType) {
  await axios.put(API_HOST + '/work_changes/comment/',
      {id: id, text: text},
      axiosConfig)
}

async function dateChange({id, date}: workDateType) {
  await axios.put(API_HOST + '/work_changes/date/',
      {id: id, date: date},
      axiosConfig)
}

async function typeChange({id, typeId}: workTypeType) {
  await axios.put(API_HOST + '/work_changes/type/',
      {id: id, typeId: typeId},
      axiosConfig)
}

</script>

<template>
  <DataTable :value="workData" tableStyle="min-width: 50rem" v-if="loaded">
    <Column field="date" header="Дата">
      <template #body="slotProps">
        <DateField
            :workId="slotProps.data.id"
            :date="slotProps.data.date"
            @change="dateChange($event)"/>
      </template>
    </Column>

    <Column field="type" header="Тип">
      <template #body="slotProps">
        <typeSelector
            :workId="slotProps.data.id"
            :typeId="slotProps.data.type"
            @change="typeChange"/>
      </template>
    </Column>

    <Column field="comment" header="Комментарий">
      <template #body="slotProps">
        <CommentField
            :workId="slotProps.data.id"
            :workComment="slotProps.data.comment"
            @change="commentChange($event)"/>
      </template>
    </Column>
  </DataTable>
</template>

