import {
    byUserData,
    capacityDataModuleStateType,
    capacityDataModuleType,
    CapacityDataType,
    ScaleType,
    teamData
} from "@/store/capacity/types";
import {capacityDataContext} from "@/store/types";
import getISOWeeksInYear from "@/utils/getISOWeeksInYear";
import getDaysInMonth from "@/utils/getDaysInMonth";
import axios from "axios";
import {API_HOST} from "@/settings";

const capacityDataModule: capacityDataModuleType = {
        state: () => ({
            // @ts-ignore
            capacityData: undefined,
            scale: 'week',
            month: new Date().getMonth(),
            year: new Date().getFullYear(),
            showInactiveUsers: true,
        }),


        mutations: {
            setScale(state: capacityDataModuleStateType, scaleValue: ScaleType): void {
                state.scale = scaleValue
            },
            setCapacityData(state: capacityDataModuleStateType, data: CapacityDataType): void {
                state.capacityData = data;
            },
            setDate(state: capacityDataModuleStateType, date: { year: number, month: number }): void {
                state.year = date.year;
                state.month = date.month;
            },
            setYear(state: capacityDataModuleStateType, year: number): void {
                state.year = year;
            },
            setMonth(state: capacityDataModuleStateType, month: number): void {
                state.month = month
            },
            switchShowInactiveUsers(state: capacityDataModuleStateType): void {
                state.showInactiveUsers = !state.showInactiveUsers
            }
        },

        getters: {
            getCapacityData(state: capacityDataModuleStateType): CapacityDataType {
                return state.capacityData;
            },
            getScale(state: capacityDataModuleStateType): ScaleType {
                return state.scale
            },
            getYear(state: capacityDataModuleStateType): number {
                return state.year
            },
            getMonth(state: capacityDataModuleStateType): number {
                return state.month
            },
            getByUserData(state: capacityDataModuleStateType): byUserData[] {

                let columnCount

                if (state.scale === 'week') {
                    columnCount = getISOWeeksInYear(state.year);
                } else if (state.scale === 'day') {
                    columnCount = getDaysInMonth(state.year, state.month);
                } else {
                    columnCount = 12;
                }


                const groupedData = new Map<number, byUserData>();

                for (const entry of state.capacityData) {
                    if (!groupedData.has(entry.userId) && (entry.userActive || state.showInactiveUsers)) {
                        // Инициализируем структуру для нового пользователя
                        groupedData.set(entry.userId, {
                            teamName: entry.teamName,
                            userName: entry.userName,
                            userId: entry.userId,
                            teamId: entry.teamId,
                            userActive: entry.userActive,
                            data: Array(columnCount).fill(null)  // Заполняем массив дней null
                        });
                    }

                    const userEntry = groupedData.get(entry.userId);
                    if (userEntry) {
                        if (state.scale === 'day') {
                            // @ts-ignore
                            userEntry.data[entry.day - 1] = entry;
                        } else if (state.scale === 'week') {
                            // @ts-ignore
                            userEntry.data[entry.week - 1] = entry;
                        } else {
                            // @ts-ignore
                            userEntry.data[entry.month - 1] = entry;
                        }

                    }
                }
                return Array.from(groupedData.values());
            },
            getTeamList(state: capacityDataModuleStateType): teamData[] {
                const uniqueTeams: Map<number, teamData> = new Map<number, teamData>();

                state.capacityData.forEach(item => {
                    if (!uniqueTeams.has(item.teamId)) {
                        uniqueTeams.set(item.teamId, {teamId: item.teamId, teamName: item.teamName});
                    }
                });

                return Array.from(uniqueTeams.values());
            },
            getColumnCount(state: capacityDataModuleStateType): number {
                if (state.scale === 'week') {
                    return getISOWeeksInYear(state.year);
                } else if (state.scale === 'day') {
                    return getDaysInMonth(state.year, state.month);
                } else {
                    return 12;
                }
            },
            getShowInactiveUsers(state: capacityDataModuleStateType): boolean {
                return state.showInactiveUsers
            },
        },

        actions: {

            async loadData({state, commit}: capacityDataContext): Promise<void> {

                if (state.scale === 'week') {

                    commit('setCapacityData',
                        (await axios.get(API_HOST + '/capacity_data/week/' + state.year + '/')).data);


                } else if (state.scale === 'month') {

                    commit('setCapacityData',
                        (await axios.get(API_HOST + '/capacity_data/month/' + state.year + '/')).data);

                } else if (state.scale === 'day') {

                    commit('setCapacityData',
                        (await axios.get(API_HOST + '/capacity_data/day/' + state.year + '/' + state.month + '/')).data);

                } else {
                    console.error('bad capacity data')
                }
            },

            async nextPeriod({state, commit, dispatch}
                                 :
                                 capacityDataContext
            ):
                Promise<void> {
                if (state.scale === 'day'
                ) {
                    if (state.month == 12) {
                        commit('setDate', {year: state.year + 1, month: 1})
                    } else {
                        commit('setMonth', state.month + 1)
                    }
                } else if (state.scale === 'week' || state.scale === 'month') {
                    commit('setYear', state.year + 1)

                } else {
                    console.error('wrong scale parameter')
                }
                await dispatch('loadData')
            },
            async prevPeriod({state, commit, dispatch}
                                 :
                                 capacityDataContext
            ):
                Promise<void> {
                if (state.scale === 'day'
                ) {
                    if (state.month == 1) {
                        commit('setDate', {year: state.year - 1, month: 12})
                    } else {
                        commit('setMonth', state.month - 1)
                    }
                } else if (state.scale === 'week' || state.scale === 'month') {
                    commit('setYear', state.year - 1)
                } else {
                    console.error('wrong scale parameter')
                }
                await dispatch('loadData')
            },
            async nowPeriod({commit, dispatch}
                                :
                                capacityDataContext
            ):
                Promise<void> {
                commit('setDate',
                    {
                        year: new Date().getFullYear(), month:
                            new Date().getMonth()
                    }
                )
                await dispatch('loadData')
            }
        },

        namespaced: true
    }
;

export default capacityDataModule;