<script setup lang="ts">
import store from "@/store";
import {computed, ComputedRef, onBeforeMount, Ref, ref} from "vue";
import {BaseDataType, ScaleType, teamData} from "@/store/capacity/types";
import CapacityHeaders from "@/components/Capacity/CapacityHeaders/CapacityHeaders.vue";
import {ToggleCeilEventType, ToggleType} from "@/components/Capacity/types";
import Popover from 'primevue/popover';
import PopoverData from "@/components/Capacity/PopoverData/PopoverData.vue";
import CapacityTeamsTable from "@/components/Capacity/CapacityTeamsTable/CapacityTeamsTable.vue";

const isDataLoaded = ref(false);

onBeforeMount(async () => {
  await store.dispatch('capacityData/loadData')
  isDataLoaded.value = true;
})

const teams: ComputedRef<teamData[]> = computed(() => {
  if (isDataLoaded.value) {
    return store.getters['capacityData/getTeamList'];
  }
  return [];
})

const scale: ComputedRef<ScaleType> = computed(() => store.getters["capacityData/getScale"])


const op = ref();
// @ts-ignore
const toggleDetail: Ref<BaseDataType> = ref();
// @ts-ignore
const toggleTypeParam: Ref<ToggleType> = ref();

const toggle = (event: ToggleCeilEventType) => {
  if (event.data) {
    toggleDetail.value = event.data;
    toggleTypeParam.value = event.type;

    if (event.show) {
      op.value.toggle(event.event);
    } else {
      op.value.hide();
    }
  } else {
    op.value.hide();
  }
};

</script>

<template>
  <div class="flex overflow-auto flex-col scroll-div bg-gray-100 capacity-variables"
       :class="scale==='month' ? ['items-center'] : []"
  >
    <!--       v-dragscroll>-->
    <CapacityHeaders/>

    <CapacityTeamsTable v-for="team in teams"
                        :team-info="team"
                        @toggle-ceil="toggle($event)"/>
  </div>
  <Popover ref="op" style="--p-popover-background: #1e282d">
    <PopoverData :toggle-detail="toggleDetail" :toggle-type-param="toggleTypeParam"/>
  </Popover>
</template>

<style scoped lang="scss">
.scroll-div {
  height: 100vh;
  max-height: 100vh;
}


</style>


<style>
.capacity-variables {
  --name-size: 256px;
  --ceil-widh: 80px;
}

.name_ceil {
  min-width: var(--name-size);
  max-width: var(--name-size);
}

</style>