<script setup lang="ts">
import {computed, ComputedRef, ref, Ref} from "vue";
import {ScaleType} from "@/store/capacity/types";
import store from "@/store";
import SelectButton from "primevue/selectbutton";

// getter
// ---------------------------------------------------------------------------------------------------------------------

const scale: ComputedRef<ScaleType> = computed(() => store.getters["capacityData/getScale"])

// consts
// ---------------------------------------------------------------------------------------------------------------------

const selectedScale: Ref<ScaleType> = ref(scale.value)

const timeOptions: Ref<{ label: string, value: string }[]> = ref([
  {label: 'М', value: 'month'},
  {label: 'Н', value: 'week'},
  {label: 'Д', value: 'day'}]);

// procedures
// ---------------------------------------------------------------------------------------------------------------------

function setScale() {
  store.commit('capacityData/setScale', selectedScale.value);
  store.dispatch('capacityData/loadData')
}
</script>

<template>
  <div class="grid content-center mt-2">
    <SelectButton
        :allowEmpty="false"
        class="h-[40px] self-center"
        v-model="selectedScale"
        :options="timeOptions"
        option-value="value"
        option-label="label"
        @change="setScale()"
        style="outline: none;">
    </SelectButton>
  </div>
</template>

